<template>
  <div class="translate" v-if="current">
    <top-info
      title=""
      :currentIndex="currentIndex"
      :len="len"
      :studyMode="studyMode"
      @toggleStudyMode="studyMode = !studyMode"
    />
    <p v-if="showLastIndex"> <span class="blue">{{lastIndex + 1}}</span> 
    <span class="blue" @click="proceed" type="text" v-if="lastIndex"> <i class="el-icon-arrow-right el-icon--right"></i></span></p>
    <p>{{studyMode ? current['eng-help'] : current.eng}}</p>
    <p class="green" v-if="percent && percent > 0.6">{{ this.percent | showPercent }}</p>
    <p class="b" v-if="studyMode">{{current['zh-cn']}}</p>
    <el-input v-if="!studyMode" @blur="handleBlur" class="input" placeholder="" type="textarea" rows="2" v-model="currentVal"/>
    <controller
      @changeIndex="changeIndex" 
      :currentIndex="currentIndex"
      :studyMode="studyMode"
      :Ans="current['zh-cn']"
      :len="len"
    />
  </div>
</template>

<script>
import strSimilarity2Percent from '@/util'
import Controller from '@/components/Controller'
import TopInfo from '@/components/TopInfo'
import translate from '@/data/translate'

export default {
  components: { Controller, TopInfo },
  data () {
    return {
      currentIndex: 0,
      currentVal: null,
      percent: null,
      len: null,
      lastIndex: null,
      studyMode: false
    }
  },

  methods: {
    changeIndex (target) {
      this.currentIndex = target
    },
    proceed () {
      this.currentIndex = this.lastIndex
    },
    handleBlur () {
      if (this.percent >= .8) {
        this.$message({
          duration: 1000,
          type: 'success',
          message: '',
          offset: 0
        })
        this.currentIndex++
      }
    }
  },

  filters: {
    showPercent (val) {
      return Number(val * 100).toFixed(2) + '%'
    }
  },

  computed: {
    current () {
      return translate.list[this.currentIndex]
    },
    showLastIndex () {
      return this.lastIndex && (this.currentIndex !== this.lastIndex)
    }
  },

  watch: {
    currentVal (val) {
      if (!val) return
      this.percent = strSimilarity2Percent(val, this.current['zh-cn'])
      if (val[val.length - 1] === '\n') {
        this.handleBlur()
      }
    },
    currentIndex () {
      this.lastIndex = null
      this.currentVal = null
      this.percent = null
      localStorage.setItem('lastIndex', this.currentIndex)
    }
  },

  mounted () {
    this.len = translate.list.length
    const lastIndex = localStorage.getItem('lastIndex') || localStorage.getItem('lastIndex')
    if (lastIndex) {
      this.lastIndex = parseInt(lastIndex)
    }
  }
}
</script>

